import React from 'react';
import { Icon, Badge, Dropdown, Menu, Modal } from 'antd';
import screenfull, { Screenfull } from 'screenfull';
import { inject, observer } from 'mobx-react';
import { Link, withRouter } from 'react-router-dom';
import { isAuthenticated, getUsername } from '../utils/Session';

import { RouteComponentProps } from 'react-router';

interface MyProps extends RouteComponentProps {
	onToggle: Function;
	collapsed: boolean;
	appStore?: any;
}

//withRouter一定要写在前面，不然路由变化不会反映到props中去
// @withRouter
@inject('appStore')
@observer
class HeaderBar extends React.Component<MyProps> {
	state = {
		icon: 'arrows-alt',
		count: 0,
		visible: false,
		avatar: require('../assets/img/04.jpg')
	};

	getScreenFull(): Screenfull {
		return screenfull as Screenfull;
	}

	componentDidMount() {
		this.getScreenFull().onchange(this.onFullscreenChange.bind(this));
	}
	componentWillUnmount() {
		this.getScreenFull().off('change', this.onFullscreenChange);
	}

	onFullscreenChange() {
		this.setState({
			icon: this.getScreenFull().isFullscreen ? 'shrink' : 'arrows-alt'
		});
	}

	toggle = () => {
		this.props.onToggle();
	};
	screenfullToggle = () => {
		if (this.getScreenFull().enabled) {
			this.getScreenFull().toggle();
		}
	};
	logout = () => {
		this.props.appStore.toggleLogin(false);
		this.props.history.push(this.props.location.pathname);
	};

	render() {
		const { icon, count, visible, avatar } = this.state;
		const { appStore, collapsed, location } = this.props;
		const notLogin = (
			<div>
				<Link
					to={{ pathname: '/login', state: { from: location } }}
					style={{ color: 'rgba(0, 0, 0, 0.65)' }}
				>
					登录
				</Link>
				&nbsp;
				<img src={require('../assets/img/defaultUser.jpg')} alt="" />
			</div>
		);
		const menu = (
			<Menu className="menu">
				<Menu.ItemGroup title="用户中心" className="menu-group">
					<Menu.Item>你好 - {getUsername()}</Menu.Item>
					{/* <Menu.Item>个人信息</Menu.Item> */}
					<Menu.Item>
						<span onClick={this.logout}>退出登录</span>
					</Menu.Item>
				</Menu.ItemGroup>
				{/* <Menu.ItemGroup title="设置中心" className="menu-group">
					<Menu.Item>个人设置</Menu.Item>
					<Menu.Item>系统设置</Menu.Item>
				</Menu.ItemGroup> */}
			</Menu>
		);
		const login = (
			<Dropdown overlay={menu}>
				<img onClick={() => this.setState({ visible: true })} src={avatar} alt="" />
			</Dropdown>
		);
		return (
			<div id="headerbar">
				<Icon
					type={collapsed ? 'menu-unfold' : 'menu-fold'}
					className="trigger"
					onClick={this.toggle}
				/>
				<div style={{ lineHeight: '64px', float: 'right' }}>
					<ul className="header-ul">
						<li>
							<Icon type={icon} onClick={this.screenfullToggle} />
						</li>
						<li onClick={() => this.setState({ count: 0 })}>
							<Badge count={appStore.isLogin ? count : 0} style={{ marginRight: -17 }}>
								<Icon type="notification" />
							</Badge>
						</li>
						<li>{appStore.isLogin ? login : notLogin}</li>
					</ul>
				</div>
				<Modal
					footer={null}
					closable={false}
					visible={visible}
					wrapClassName="vertical-center-modal"
					onCancel={() => this.setState({ visible: false })}
				>
					<img src={avatar} alt="" width="100%" />
				</Modal>
			</div>
		);
	}
}

export default withRouter(HeaderBar);
