import React from 'react';
import { Layout } from 'antd';
import SiderNav from '../../Components/SiderNav';
import PrivateRoute from '../../Components/PrivateRoute';
import { Switch } from 'react-router';
import Home from '../Home';
import HeaderBar from '../../Components/Header';
import BasicSetting from '../BasicSetting';

const { Sider, Header, Content, Footer } = Layout;

export default class Empty extends React.Component {
	state = {
		collapsed: false
	};

	toggle = () => {
		// console.log(this)  状态提升后，到底是谁调用的它
		this.setState({
			collapsed: !this.state.collapsed
		});
	};
	render() {
		// 设置Sider的minHeight可以使左右自适应对齐
		console.log('Empty.render');
		return (
			<div id="page">
				<Layout>
					<Sider collapsible trigger={null} collapsed={this.state.collapsed}>
						<SiderNav />
					</Sider>
					<Layout>
						<Header style={{ background: '#fff', padding: '0 16px' }}>
							<HeaderBar collapsed={this.state.collapsed} onToggle={this.toggle}></HeaderBar>
						</Header>
						<Content>
							<div style={{ padding: 16, position: 'relative', height: '100%' }}>
								<Switch>
									<PrivateRoute exact path="/home" component={Home} />
									<PrivateRoute exact path="/home/general" component={BasicSetting} />
								</Switch>
							</div>
						</Content>
						<Footer style={{ textAlign: 'center' }}>com.jj.pn copyright</Footer>
					</Layout>
				</Layout>
			</div>
		);
	}
}
