import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import './assets/font/iconfont.css';
import { LocaleProvider } from 'antd';
// slint-disable-next-line
import { BrowserRouter } from 'react-router-dom';
import { observable, action } from 'mobx';

/*eslint-disable @typescript-eslint/camelcase*/
import zh_CN from 'antd/lib/locale-provider/zh_CN';
import { isAuthenticated, authenticateSuccess, logout } from './utils/Session';
import { Provider } from 'mobx-react';
import Bmob from 'hydrogen-js-sdk';

Bmob.initialize('a63ecb079ce63631a21cf7d6b6bdc8ea', 'b4a13ef02129fb438fd7b304f2397fb3');

class AppStore {
	@observable isLogin = !!isAuthenticated(); //利用cookie来判断用户是否登录，避免刷新页面后登录状态丢失
	@observable users: any; //模拟用户数据库
	@observable loginUser = {}; //当前登录用户信息

	@action
	toggleLogin(flag: boolean, info: any = {}): void {
		this.loginUser = info; //设置登录用户信息
		if (flag) {
			authenticateSuccess(info.username);
			this.isLogin = true;
		} else {
			logout();
			this.isLogin = false;
		}
	}
	@action initUsers(): void {
		const localUsers = localStorage['users'] ? JSON.parse(localStorage['users']) : [];
		this.users = [{ username: 'admin', password: 'admin' }, ...localUsers];
	}
}

const store = {
	appStore: new AppStore()
};

let App = require('./App').default;
const render = (): void => {
	ReactDOM.render(
		<BrowserRouter>
			<LocaleProvider locale={zh_CN}>
				<Provider {...store}>
					<App />
				</Provider>
			</LocaleProvider>
		</BrowserRouter>,
		document.getElementById('root')
	);
};

render();

const hot = (module as any).hot;
if (hot) {
	//hot.decline('./store.js');
	// hot.accept(['App', './router'], (): void => {
	hot.accept(['./src/App.tsx'], (): void => {
		console.log('***** accept App hot update 1*******');
		// lastHotUpdateTime = Date.now();
		App = require('./App').default;
		render();
		console.log('***** accept App hot update 2*******');
	});
}
