import React from 'react';
import './home.css';
import { Carousel } from 'antd';
import { findDOMNode } from 'react-dom';

class Home extends React.Component<{}, { myHeight?: number }> {
	state = {
		myHeight: 0
	};
	carousel!: Carousel;
	myCar = React.createRef<Carousel>();
	myHold!: HTMLElement;
	imgs = [
		'http://pic1.win4000.com/tj/2019-04-01/5ca1cc356e8f0.jpg',
		'http://pic1.win4000.com/wallpaper/2019-06-19/5d09a6ad02002.jpg',
		'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1560776070627&di=420bc149f79b17b91e9db88cb389f574&imgtype=0&src=http%3A%2F%2Fpic1.win4000.com%2Fwallpaper%2F2017-11-20%2F5a127cc0b24eb.jpg',
		'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1560776070627&di=f4c3e863f96d1324c0a3468c3a10a5b1&imgtype=0&src=http%3A%2F%2Fpic1.win4000.com%2Fwallpaper%2Fa%2F583653ffd010c.jpg'
	];

	componentDidMount() {
		console.log('Home.mount', this.myCar, this.myHold);
		const fdn = findDOMNode;
		const root = fdn(this) as HTMLElement;
		// setInterval(() => {
		setTimeout(() => {
			if (this.myHold != null) {
				console.log('hold', this.myHold.clientHeight);
				this.setState({ myHeight: this.myHold.clientHeight - 0.0 });
				this.myHold.remove();
				// root.style.height = this.myHold.clientHeight + 'px';
			}

			const btns = root.querySelectorAll<HTMLElement>('button');
			if (btns != null && btns.length > 0) {
				// divItem.style.height = this.myHold.clientHeight + 'px';
				// btns[0].style.left = '1px';
				// btns[1].style.right = '1px';
			}
		}, 1);
	}

	render() {
		const { myHeight } = this.state;
		console.log('Home.render', myHeight);
		// this.imgs = [];
		const item = this.imgs[0];
		return (
			<div className="home">
				<div ref={el => (this.myHold = el as HTMLElement)} className="hold">
					myhold
				</div>
				<Carousel ref={this.myCar} arrows className="my-carousel">
					{this.imgs.map((item, k) => (
						<div className="item" key={item}>
							<div
								style={{
									// width: 399,
									// width: '100%',
									height: myHeight,
									color: '#0000000a',
									// backgroundColor: 'grey',
									backgroundImage: `url(${item})`
								}}
							>
								{k}
							</div>
						</div>
					))}
				</Carousel>
			</div>
		);
	}
}

export default Home;
